<template>
  <div class="card">
    <div class="card-header">
      Exam01AsyncControl
    </div>

    <div class="card-body">
      <div>
        <button @click="handleBtn1" class="btn btn-info btn-sm mr-2">비동기 작업1</button>
        <button @click="handleBtn2" class="btn btn-info btn-sm mr-2">비동기 작업2</button>
      </div>

      <div v-if="!loading" class="mt-3">
        <div>result: {{ result }}</div>
      </div>

      <div v-if="loading" class="spinner-border text-info mt-3" role="status">
        <span class="sr-only">Loading...</span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";

const result = ref("");
// 스피너 상태 제어용 변수
const loading = ref(false);

function handleBtn1() {
  loading.value = true;
  work()
  .then((data) => {
    result.value = data;
  })
  .catch((error) => {
    console.log(error);
  })
  .finally(() => {
    loading.value = false;
  });
}

async function handleBtn2() {
  try {
    loading.value = true;
    const data = await work();
    result.value = data;
  } catch(error) {
    console.log(error);
  } finally {
    loading.value = false;
  }
}

// 비동기 작업 정의
function work() {
  const promise = new Promise((resolve, reject) => {
    setTimeout(() => {
      // 3초가 지난 뒤 실행
      resolve("value");
    }, 3000);
  });
  // then ~ catch~finally 작성시 실행이 되어버리기 때문에 Promise만 작성하여 return, Axios가 이런 식으로 되어있음
  return promise;
}
</script>

